/* MatchSchedule.css */
.match-schedule-container {
  text-align: center;
  margin: 15px;
  font-size: 25px;
}

.match-schedule-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.match-schedule-table th,
.match-schedule-table td {
  border: 1px solid #ababab;
  padding: 20px;
  text-align: left;
}

.match-schedule-table th {
  background-color: #f2f2f2;
}

td.bold-text {
  font-weight: bold;
  padding: 10px; /* Adjust the padding as needed */
  font-size: 16px; /* Adjust the font size as needed */
}

tbody tr.bold-text td {
  font-weight: bold;
  padding: 10px; /* Adjust the padding as needed */
  font-size: 26px; /* Adjust the font size as needed */
}

/* Add the following media query for mobile devices */
@media only screen and (max-width: 767px) {
  .match-schedule-table td.bold-text {
    font-weight: bold;
    padding: 10px; /* Adjust the padding as needed */
    font-size: 16px; /* Adjust the font size as needed */
  }

  tbody tr.bold-text td {
    font-weight: bold;
    padding: 10px; /* Adjust the padding as needed */
    font-size: 26px; /* Adjust the font size as needed */
  }
}

